/*==============================================================
*												NOT FOUND
=============================================================== */
.section-not-found {
  padding-top: 120px;
  padding-bottom: 60px;
  min-height: 100vh;
  display: flex;
  line-height: 1.3;
}
.section-not-found .container {
  margin-top: auto;
  margin-bottom: auto;
}
.section-not-found .section-title {
  font-size: 60px;
  color: #FD0037;
  margin-bottom: 30px;
}
.section-not-found .content {
  font-weight: bold;
  font-size: 40px;
}
@media (max-width: 767.9px) {
  .section-not-found {
    min-height: auto;
  }
  .section-not-found .content {
    font-size: 30px;
  }
}

/*==============================================================
*												SECTION INDEX ABOUT
=============================================================== */
@media (max-width: 991.9px) {
  .section__about .about__choose {
    display: block;
  }
  .section__about .link__about {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    margin: 0;
    color: #000;
    opacity: 1;
    font-size: 20px;
    background-color: rgba(0, 0, 0, 0.03);
  }
  .section__about .collapse-body {
    background-color: rgba(0, 0, 0, 0.03);
  }
  .section__about .collapse-body .content {
    padding: 20px;
    padding-top: 5px;
  }
  .section__about .collapse-body .content p {
    margin: 0;
    margin-top: 15px;
  }
  .section__about .collapse-ico {
    display: block;
    width: 20px;
    height: 2px;
    background-color: #000;
    position: relative;
    margin-left: 20px;
  }
  .section__about .collapse-ico:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 2px;
    height: 20px;
    background-color: #000;
    transition: 0.2s;
  }
  .section__about .active .collapse-ico:after {
    height: 0px;
  }
}

.collapse-body:not(.active) {
  display: none;
}